import React from 'react'
import styled from 'styled-components';
import { Grid, Segment, Image, Label } from 'semantic-ui-react';

import Sera from '../assets/img/sera.png';
import Raffe from '../assets/img/raffe.png';
import Box from '../assets/img/box.png';
import Museu from '../assets/img/museu.png'

const Wrapper = styled.div`
  
`;
const Paragraph = styled.p`
    font-size: 14px;
    text-align: justify;
`

const Title = styled.h4`
    font-size: 28px;
    margin: 0;
`
const SubTitle = styled.h5`
    margin: 0 0 15px 0;
    font-size: 20px;
    font-weight: 300;
    color: grey;
`
const projetos = () => {
    return (
        <Wrapper>
            <Grid stackable >
                <Grid.Row as={Segment} >
                    <Grid.Column width="7" >
                        <Image src={Museu} rounded label={{content: 'NodeJs & ThreeJS', ribbon: true}} />
                    </Grid.Column>        
                    <Grid.Column width="9">
                        <Title>Museu de Ciências Morfológicas</Title>
                        <hr/>
                        <SubTitle>Um projeto de acessibilidade.</SubTitle>
                        <Paragraph>
                            O projeto desenvolvido para o museu de ciências morfológicas foi criado para auxiliar todo o tipo de visitante do museu. Criando um ambiente virtual para aqueles que não podem visitar o museu e auxiliando as pessoas em condições especiais, como deficientes visuais.
                        </Paragraph>
                    </Grid.Column>        
                </Grid.Row>
                <Grid.Row as={Segment} >
                    <Grid.Column width="7" >
                        <Image src={Sera} rounded label={{content: 'Ionic & Firebase', ribbon: true}} />
                    </Grid.Column>        
                    <Grid.Column width="9">
                        <Title>Será que é TEA?</Title>
                        <hr/>
                        <SubTitle>Aplicativo campeão.</SubTitle>
                        <Paragraph>
                            O "Sera que é TEA?" foi um aplicativo desenvolvido por mim e a Psicóloga Ligia Moraes com o intuito de auxiliar pais no diagnóstico prévio de crianças com autismo, o mesmo ganhou o prêmio de ideia inovadora no StartPSI 2018.
                        </Paragraph>
                    </Grid.Column>        
                </Grid.Row>
                <Grid.Row as={Segment} >
                    <Grid.Column width="7" >
                        <Image src={Raffe} rounded label={{content: 'Ionic & Firebase', ribbon: true}} />
                    </Grid.Column>        
                    <Grid.Column width="9">
                        <Title>
Hackathon Raffe</Title>
                        <hr/>
                        <SubTitle>48h pra mudar uma empresa.</SubTitle>
                        <Paragraph>
                        Nos desafiaram a melhorar 3 conceitos da empresa em 48h, com apenas 3 participantes nosso grupo conseguiu criar soluções para melhorar a produção, o controle interno do estoque da empresa e um aplicativo pra melhor conectar o cliente com a marca.
                        </Paragraph>
                    </Grid.Column>        
                </Grid.Row>
                <Grid.Row as={Segment} >
                    <Grid.Column width="7" >
                        <Image src={Box} rounded label={{content: 'React Native', ribbon: true}} />
                    </Grid.Column>        
                    <Grid.Column width="9">
                        <Title>My Box</Title>
                        <hr/>
                        <SubTitle>Para solucionar um problema.</SubTitle>
                        <Paragraph>
                        O My Box é um aplicativo que foi criado para tentar amenizar os problemas que estavam acontecendo em um Box de crossfit, o aplicativo foi desenvolvido para que os alunos fizessem o Check-In de formar simples e rápida, além de acompanhar o rendimento dos mesmos.
                        </Paragraph>
                    </Grid.Column>        
                </Grid.Row>
                
                
            </Grid>
        </Wrapper>
    )
}

export default projetos
