import React from 'react'
import styled from 'styled-components';

const Header = styled.h3`
  width: 100%;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  font-size: 28px;
  &::before, &::after {
    content: '';
    display: block;
    width: 70px;
    height: 2px;
    background-color: black;
    margin: 0 15px;
  }
`;

const header = (props) => {
    return (
        <Header>{props.children}</Header>
    )
}

export default header
