import React from 'react'
import styled from 'styled-components';
import { Header, Segment, Divider, Popup, Grid, Image } from 'semantic-ui-react';
import Cab from '../components/header'
import Foto from '../assets/img/foto.jpg';
const expItems = [
    { 
    local: {
        principal: 'Universidade Fedral do Rio Grande do Norte - UFRN',
        especifico : 'Bacharelado em Ciência e Tecnologia'
        },
    periodo: '2015 - 2019',    
    tipo: 'Graduado'
    },
    { 
    local: {
        principal: 'Universidade Fedral do Rio Grande do Norte - UFRN',
        especifico : 'Engenharia da Computação'
        },
    periodo: '2019 - ...',    
    tipo: 'Cursando'
    },
    {
    local: {
        principal: 'Laboratórios Natalnet',
        especifico : 'Laboratório de Tecnologias Educacionais, Assistivas e Multimidia - TEAM'
        },
    periodo: '2019 - ...',    
    tipo: 'Voluntário'
    },
    { 
    local: {
        principal: 'Tribunal de Justiça do Rio Grande do Norte - TJRN',
        especifico: 'Departamento de Projetos e Sistemas - DPS'
        },
    periodo: '2018 - 2019',    
    tipo: 'Estagiário'
    },
    {
    local: {
        principal: 'Museu de Ciências Morfológicas - UFRN',
        especifico : 'Centro de Biociências - CB'
        },
    periodo: '2019 - ...',    
    tipo: 'Bolsista'
    },

]
const conItems = [
    {name: 'React', icon: 'devicon-react-original'},
    {name: 'Angular', icon: 'devicon-angularjs-plain'},
    {name: 'Sass', icon: 'devicon-sass-original'},
    {name: 'Github', icon: 'devicon-github-plain'},
    {name: 'Gulp', icon: 'devicon-gulp-plain'},
    {name: 'Node JS', icon: 'devicon-nodejs-plain'},
    {name: 'Express', icon: 'devicon-express-original'},
    {name: 'Mongo DB', icon: 'devicon-mongodb-plain'},
    {name: 'MySQL', icon: 'devicon-mysql-plain'},
    {name: 'C++', icon: 'devicon-cplusplus-plain'},
    
]
const curriculum = () => {
    return (
    <Wrapper>
        <Grid stackable>
            <Grid.Row>
                <Grid.Column floated="left" width="6">
                    <Image src={Foto} rounded />
                </Grid.Column>
                <Grid.Column floated='right' width="10" >
                    <Grid.Row >
                        <Grid.Column textAlign="right">
                            <Nome>Alek Tobias Barreira Lima</Nome>
                            <Sub>Brasil • Rio Grande do Norte • Natal</Sub>
                            <Divider hidden />
                        </Grid.Column>
                    </Grid.Row>
                    <Grid.Row >
                        <Paragraph>
                            Atualmente estudo na Universisade Federal do Rio Grande do Norte (UFRN), onde tive a oportunidade de aprender e crescer com auxílio dos professores e colegas do Laboratório TEAM. Assim pude me envolver em projetos que buscavam solucionar problemas cotidianos por meio da tecnologia.
                        </Paragraph>
                        <Paragraph>
                            Nossos principais projetos envolveram Internet das coisa e Indústria 4.0 e sempre estivemos focados em encontrar a melhor solução e deixá-la da forma mais acessível possível.
                        </Paragraph>
                    </Grid.Row>
                </Grid.Column>
            </Grid.Row>
        </Grid>

        <Segment>
            <div id="experiencias">
                <Cab>Experiências</Cab>
                {
                expItems.map((exp, i) => {
                    return(
                        <ExpItem key={i}>
                            <div>
                                <Header as="h3">{exp.local.principal}</Header>
                                <span className="sub">{exp.local.especifico}</span>
                            </div>
                            <div className="periodo">
                                <span>{exp.tipo}</span>
                                <span className="data">{exp.periodo}</span>
                            </div>
                            <Divider hidden /> 
                        </ExpItem>
                    );
                })
                }
            </div>
            
            <div id="conhecimentos">
                <Cab>Conhecimentos</Cab>
                <ConDiv>
                {
                conItems.map((item, i) => {
                        
                    return (
                        <Popup key={i} trigger={<i className={item.icon}></i>} 
                        content={item.name} position='bottom center'/>
                    )
                })
                }
                </ConDiv>
                
                
            </div>
        </Segment>
    </Wrapper>
    )
}

const Wrapper = styled.main`
    i {
        font-size: 48px;
    }
`
const Nome = styled.h2`
    text-align: end;
    margin-bottom: 5px;
    font-size: 38px;
`
const Sub = styled.h5`
    font-weight: 300;
    text-align: end;
    color: lightgrey;
    font-size: 16px;
    margin-bottom: 20px;
    font-size: 18px;
    margin: 0;
`
const Paragraph = styled.p`
    text-align: justify;
    font-size: 1.1em;
`
const ExpItem = styled.div`
    display: flex;
    width: 80%;
    margin: 35px auto;
    justify-content: space-between;
    div.periodo {
        margin: 0 0 0 auto;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        width: 120px;
        span.data{
            font-style: italic;
        }
    }
    
`

const ConDiv = styled.div`
    display: flex;
    justify-content: center;
    margin: 10px auto;
    flex-wrap: wrap;
    max-width: 400px;
    i{
        padding: 5px;
        margin: 10px;
    }
`

export default curriculum;
