import React from 'react'
import styled from 'styled-components';

export const Wrapper = styled.div`
  display: flex;
  flex-direction: column;
  h2{
      font-size: 32px;
      margin: 0;
  }
  a, span#cel, span#git {
    font-size: 18px;
    margin: 5px 0; 
    color: grey;
  }
`;

const contatos = () => {
    return (
        <Wrapper>
            <h2>Alek Tobias Barreira Lima</h2>
            <a href="mailto:alektobias@gmail.com" target="_top">alektobias@gmail.com</a>
            <span id="cel">+55 (84) 9 9177-1882</span>
            <span id="git">https://github.com/alektobias</span>
        </Wrapper>
    )
}

export default contatos
